<template>
	<view :class="{body:models=='1'||models=='', bodyNight:models=='0'}">
		<!-- 弹窗 -->
		<view class="model" v-if="showAudition">
			<view class="models">
				<img src="../../static/index/modelBg.png">
				<view class="closeModel" @click="closeAudition">×</view>
				<view class="contents">
					<view class="modelTitle">请输入邀请码</view>
					<input type="text" v-model="auditionCode"/>
					<view class="buttons">
						<button class="sure" @click="sendAuditionCode">确定</button>
						<button class="contact" open-type="contact">联系客服</button>
					</view>
				</view>
			</view>
		</view>
		<view class="tabList">
			<view class="cell" v-for="(item,index) in data" :key="index" @click="checkedTab(item.id)">{{item.title}}</view>
		</view>
		<view class="starList">
			<!-- 单词列表 -->
			<view class="">
				<view v-for="(item,index) in dataList.data" :key="index" class="express_content">
					<view>
						<view class="h5">{{item.title}}</view>
						<view class="h6">{{item.create_time}}</view>
					</view>
					<view class="right">
						<view class="look" @click="toLook(item.id)">点击查看</view>
						<view class="times">浏览记录{{item.view_num}}次</view>
					</view>
				</view>
				<!-- <view class="tip" v-if="data<1">
					暂无收藏哦~
				</view> -->
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { analysisBrushMenu, analysisBrushList, analysis, invitation } from '@/common/http.api.js';
	export default {
		data() {
			return {
				showAudition:false, //弹窗显示状态
				auditionCode:'', //邀请码
				current:0, // 当前选中
				models:'1', //白天模式
				list1: [],
				data:{},
				dataList:{}
			}
		},
		onLoad() {
			this.models = store.state.model
			this.token = store.state.vuex_token
			analysisBrushMenu({token:this.token}).then(res=>{
				console.log(res)
				for(let i in res.data.data){
					this.list1.push({name:res.data.data[i].title})
				}
				console.log(this.list1)
				this.data = res.data.data
				analysisBrushList({id:res.data.data[0].id}).then(res=>{
					console.log(res)
					this.dataList = res.data.data
				}).catch(err=>{
					console.log(err)
				})
			}).catch(err=>{
				console.log(err)
			})
			if(this.models=='0'){
				console.log('进入夜间模式')
				uni.setNavigationBarColor({
				    frontColor: '#ffffff',
				    backgroundColor: '#303030',
				    animation: {
				        duration: 0,
				        timingFunc: 'easeIn'
				    }
				})
			}else{
				console.log('进入白天模式')
				uni.setNavigationBarColor({
				    frontColor: '#ffffff',
				    backgroundColor: '#6ab4ff',
				    animation: {
				        duration: 0,
				        timingFunc: 'easeIn'
				    }
				})
			}
		},
		onShow() {
			
		},
		methods: {
			// 切换tabar
			click(item) {
				console.log('item', item);
				this.current = item.index
				
			},
			// 关闭弹窗
			closeAudition() {
				this.showAudition = false
			},
			// 点击tab
			checkedTab(id) {
				analysisBrushList({id}).then(res=>{
					console.log(res)
					this.dataList = res.data.data
				}).catch(err=>{
					console.log(err)
				})
			},
			// 发送邀请码
			sendAuditionCode() {
				invitation({token:this.token, audition_code:this.auditionCode}).then(res=>{
					console.log(res)
					if(res.data.code==-1){
						this.$u.toast(res.data.data)
					}else{
						this.$u.toast(res.data.data)
						this.showAudition = false
					}
				}).catch(err=>{
					console.log(err)
				})
			},
			// 点击查看
			toLook(e) {
				analysis({token:this.token, id:e}).then(res=>{
					console.log(res)
					if(res.data.code==-1){
						this.$u.toast(res.data.data)
						this.showAudition = true
					}else{
						console.log(res.data)
						uni.downloadFile({
						  url: res.data,
						  success: function (res) {
						    var filePath = res.tempFilePath;
						    uni.openDocument({
						      filePath: filePath,
						      showMenu: true,
						      success: function (res) {
						        console.log('打开文档成功');
						      }
						    });
						  }
						});
					}
				}).catch(err=>{
					console.log(err)
				})
			}
		}
	}
</script>

<style lang="scss">
	.model{
		position: fixed;
		left: 0;
		top: 0;
		width: 100vw;
		height: 100vh;
		background-color: rgba(0, 0, 0, 0.4);
		z-index: 5;
		.models{
			width: 90%;
			left: 50%;
			right: 50%;
			position: fixed;
			margin-top: 65%;
			// border-radius: 20rpx;
			// padding: 10rpx 0;
			// background-color: #fff;
			transform: translate(-50%,-50%);
			text-align: center;
			img{
				width: 101%;
				height: 200rpx;
			}
			.contents{
				border-bottom-left-radius: 20rpx;
				border-bottom-right-radius: 20rpx;
				padding: 10rpx 0;
				margin: 0 30rpx;
				margin-top: -50rpx;
				background-color: #fff;
			}
			.closeModel{
				top: -20rpx;
				right: -10rpx;
				width: 50rpx;
				height: 50rpx;
				line-height: 50rpx;
				color: #fff;
				border: 1px solid #fff;
				font-size: 38rpx;
				text-align: center;
				position: absolute;
				border-radius: 50%;
			}
			.modelTitle{
				font-weight: 600;
				margin: 30rpx 0;
			}
			input{
				border:1rpx solid #d1d1d1;
				width: 90%;
				margin: 0 5%;
				line-height: 88rpx;
				height: 88rpx;
				border-radius: 10rpx;
			}
			.buttons{
				display: flex;
				justify-content: space-between;
				padding: 50rpx 20rpx;
				button{
					color: #fff;
					border-radius: 10rpx;
					width: 45%;
					line-height: 80rpx;
				}
				.sure{
					background-color: #e73928;
				}
				.contact{
					background-color: #c0c0c0;
				}
			}
		}
	}
	.body{
		background-color: #f7f7fb;
		height: 100vh;
		padding: 30rpx;
		.tabList{
			background-color: #fff;
		}
	}
	.bodyNight{
		background-color: #3b3b3b;
		height: 100vh;
		padding: 30rpx;
		.express_content{
			color: #fff;
			background-color: #303030 !important;
		}
		.tabList{
			background-color: #303030;
			.cell{
				color: #fff;
			}
		}
	}
	.tabList{
		padding: 20rpx;
		border-radius: 20rpx;
		display: flex;
		justify-content: space-between;
		.cell{
			font-size: 28rpx;
			padding-bottom: 20rpx;
			position: relative;
		}
		.cell::after{
			content: '';
			display: inline-block;
			width: 100rpx;
			height: 6rpx;
			background-color: #6ab4ff;
			border-radius: 20rpx;
			position: absolute;
			bottom: 0;
			left: 50%;
			right: 50%;
			transform: translate(-50%,-50%);
		}
	}
	.u-tabs{
		border-radius: 10rpx;
		padding-bottom: 20rpx;
	}
	.starList{
		.right{
			text-align: center;
			.look{
				background-color: #6ab4ff;
				border-radius: 6rpx;
				padding: 8rpx 0;
				font-size: 24rpx;
				color: #fff;
				margin-bottom: 10rpx;
			}
			.times{
				font-size: 20rpx;
				color: #858585;
			}
		}
		.tip{
			text-align: center;
		}
		.express_content{
			display: flex;
			justify-content: space-between;
			margin: 20rpx 0;
			padding: 0 20rpx;
			padding-top: 20rpx;
			border-radius: 10rpx;
			background-color: #fff;
		}
		.h5 {
			font-size: 28rpx;
			font-weight: 600;
			letter-spacing: 2rpx;
			line-height: 48rpx;
		}
		.h6 {
			font-size: 24rpx;
			line-height: 36rpx;
			color: #999;
			font-weight: 400;
			margin-bottom: 20rpx;
		}
	}
</style>